.cookies-policy-banner
  background: rgb(29,26,51)
  background: linear-gradient(60deg, rgba(29,26,51,1) 25%, rgba(54,14,115,0.8590029761904762) 100%)
  bottom: -100%
  height: auto
  overflow: hidden
  position: fixed
  width: 100%
  z-index: 200
  color: #fff
  font-weight: 400
  &__inner-wrapper
    align-items: center
    display: flex
    height: 100%
    margin: 0 auto
    padding: 25px 25px
    +md
      padding: 25px 15px
    +sm
      flex-direction: column
      padding: 25px 10px
  &__text-block
    display: flex
    max-width: 906px
    width: 100%
    +sm
      flex-direction: column
      max-width: 600px
    & .icon-wrap
      align-self: center
      max-height: 100px
      width: 6.25rem
      +sm
        display: flex
        justify-content: center
        max-height: 80px
        margin-bottom: 10px
    & .text 
      align-content: baseline
      display: flex
      flex-wrap: wrap
      margin: 0 30px 0 30px
      max-width: 746px
      +md
        margin: 0 15px 0 15px
      +sm
        display: flex
        justify-content: center
        text-align: center
        margin-bottom: 30px
      & .title
        font-size: 24px
        font-weight: 700
        margin-bottom: 15px
        +md
          font-size: 22px
        +sm
          font-size: 20px
        +xs
          font-size: 18px
        &p
          color: #ccabd8
          font-size: 20px
          margin: 5px 0
          font-weight: 400
          +md
            font-size: 14px
          &a
            text-decoration: none
            color: #a560ff
  &__button-block
    align-items: center
    display: flex
    flex-grow: 1
    height: 100%
    justify-content: flex-end
    & .accept-cookie-button
      align-items: center
      background-color: #5c49d0
      clip-path: polygon(12px 0, calc(100% - 12px) 0, 100% 50%, calc(100% - 12px) 100%, 12px 100%, 0 50%)
      color: #fff
      cursor: pointer
      display: inline-flex
      font-size: 16px
      font-weight: 700
      height: 56px
      justify-content: center
      min-width: 150px
      padding: 0 15px
      text-transform: uppercase
      transition: all 0.3s
      user-select: none
      white-space: nowrap
      width: unset
      border: none
      position: relative
      &:hover 
         background-color: #a560ff
      & .check-icon
        background-color: #fff
        display: inline-block
        height: 13px
        margin-left: 10px
        mask-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 20 20'%3E%3Cpath d='M20 4.095 17.986 2 7.554 12.848 2.014 7.05 0 9.145 7.554 17z'/%3E%3C/svg%3E")
        mask-position: center
        mask-repeat: no-repeat
        mask-size: 100%
        width: 18px

.button__content
  align-items: center
  display: flex
  flex-direction: row

@keyframes fadeIn
  0%
    bottom: -100%
  100%
    bottom: 0
