@import "./normalize"

@import "./vars"
@import "./mixins"
@import "./fonts"
@import "./sprite_generated"

  
* 
    box-sizing: border-box

body
    display: flex
    flex-direction: column
    background: #fff
    color: #ffffff
    font-family: "RobotoCondensed-Regular", Arial, sans-serif
    width: 100%
    height: 100%
    min-height: 100vh
    -webkit-font-smoothing: antialiased
    -moz-osx-font-smoothing: grayscale
    overflow-x: hidden

.container
    width: 100%
    max-width: 1920px
    min-width: 320px
    margin: 0 auto

.container-footer 
    max-width: 1920px
    margin: 0 auto
    padding: 0 30px
    @include lg 
        max-width: 1490px
        padding: 0 25px
    @include md
        max-width: 1240px
        padding: 0 20px
    @include xs
        padding: 0 15px

a
    color: #fff

p 
    margin: 0

img
    max-width: 100%

.mb-20
    margin-bottom: 10px
