.footer
  background-color: #110F1D
  color: #fff
  position: relative
  z-index: 1
  overflow: hidden
  font-family: $med
  &__smoke
    position: absolute
    bottom: 0
    left: 50%
    transform: translateX(-50%)
    object-fit: cover
    +sm
      display: none
  &__wrap
    padding: 52px 0 44px
    display: flex
    justify-content: space-between
    position: relative
    +sm
      display: block
      padding: 32px 0 38px
  &__bullet
    position: absolute
    bottom: 0
    right: 0
    transform: translateX(50%)
    z-index: -1
    +sm
      display: none
  &__main
    padding-left: 100px
    padding-right: 30px
    position: relative
    +sm
      padding: 0
      text-align: center
      margin-bottom: 18px
  &__copy
    display: block
    margin-bottom: 12px
    font-size: 16px
    font-weight: bold
    +sm
      display: flex
      flex-direction: column
      align-items: center
      margin-bottom: 8px
  &__logo
    position: absolute
    left: 0
    top: 0
    +sm
      position: static
      width: 50px
      height: 50px
      margin-bottom: 15px
      .icon
        width: 100%
        height: 100%
  &__info
    color: #78758C
    margin-bottom: 28px
    font-size: 16px
    +md 
      margin-bottom: 20px
    +xs
      font-size: 14px
      margin-bottom: 12px
    p
      margin: 0
  &__about
    color: #4D4A64
    font-size: 16px
    +xs
      font-size: 14px
    p
      margin: 0
  &__second
    max-width: 580px
    width: 100%
    +lg 
      max-width: 500px
    +sm
      margin: 0 auto
  &__box
    display: flex
    justify-content: space-between
    margin-bottom: 40px
    +sm
      flex-direction: column
      margin-bottom: 10px
  &__payment
    max-width: 250px
    display: flex
    align-items: center
    flex-wrap: wrap
    margin-bottom: -10px
    +sm
      justify-content: space-between
      margin: 0 auto
  &__payment-image
    margin-right: 18px
    margin-bottom: 10px
    &:nth-child(3n)
      margin-right: 0
  &__links
    text-decoration: none
    list-style: none
    padding: 0
    margin: 0
    display: flex
    +sm
      order: -1
      justify-content: center
      margin-bottom: 16px
  &__links-item
    &:not(:last-child)
      margin-right: 30px
  &__link
    display: flex
    align-items: center
    color: #4D4A64
    text-decoration: none
    font-weight: bold
    .icon
      margin-right: 10px

.footer-menu
  &__list
    list-style: none
    padding: 0
    margin: 0
    display: flex
    +sm
      display: block
  &__item
    &:not(:last-child)
      margin-right: 30px
      +sm
        margin: 0 0 15px
  &__link
    display: block
    font-size: 14px
    text-transform: uppercase
    font-weight: bold
    color: #EF436C
    text-decoration: none
    +sm
      text-align: center

