.main
    flex-grow: 1


.preloader
    position: fixed 
    top: 0
    left: 0
    width: 100vw 
    height: 100vh
    height: var(--app-height)
    background: black
    z-index: 101
    display: flex 
    align-items: center 
    justify-content: center 
    background: linear-gradient(72.44deg, #CC00FF 0%, #4900A5 73.5%, #22004E 100%)
    z-index: 20
    @include xxs 
        background: linear-gradient(72.44deg, #22004E 0%, #4900A5 59.36%, #6700EA 100%)
    &.hide 
        opacity: 0
        transition: opacity 0.5s ease-out
        pointer-events: none
    &__center 
        width: 588px 
        
        @include xxs 
            width: 350px 
        @media(max-width: 360px)
            width: 300px
    &__skin 
        position: absolute
        top: 50% 
        left: 50% 
        transform: translate(-50%, -50%)
        width: 120px 
        height: 120px 
        display: flex 
        align-items: center 
        justify-content: center
        img
            position: absolute
            top: 50%
            transform: translate(-50%, -50%)
            left: 50%
            width: auto
            height: auto 
            object-fit: contain
            object-position: center
            opacity: 0
        @include xxs 
            width: 72px 
            height: 72px
        @media(max-width: 360px)
            width: 50px
            height: 50px

.main-title
    margin-left: -5px
    letter-spacing: -0.05em
    font-family: $teko
    text-align: center
    text-transform: uppercase
    filter: drop-shadow(0px 0px 30px #000000)
    @media(min-width: 1921px)
        font-size: 80px
        line-height: 70%
    font-size: 4.1vw
    line-height: 2.8vw
    @include sm 
        font-size: 8.4vw
        line-height: 6.1vw
    @include xxs 
        font-size: 11.4vw
        line-height: 8.3vw
   
.wrap
    max-width: 1920px 
    height: 100vh
    height: var(--app-height)
    position: relative
    background: #fff
    overflow: hidden
    @include sm 
        display: flex
    &.active1 
        .part2
            left: 29.4%
        .part3 
            left: 54.4%
        .part4
            left: 79.4%
        @include sm
            .part1 
                width: 500px 
            .part2, .part3, .part4
                width: calc((100% - 500px) / 3)
        @include xs
            .part1 
                width: 400px 
            .part2, .part3, .part4
                width: calc((100% - 400px) / 3)   
        @include xxs
            display: flex
            .part1 
                width: calc(100vw - 100px) 
            .part2, .part3
                width: 50px     
            .part4
                width: 0
        .wrap__item
            &::before
                opacity: 0.7
    &.active2
        .part1
            left: -2.2%
        .part2
            left: 22.8%
        .part3 
            left: 52.2%
        .part4
            left: 77.2%
        @include sm
            .part2 
                width: 500px 
            .part1, .part3, .part4
                width: calc((100% - 500px) / 3)
        @include xs
            .part2 
                width: 400px 
            .part1, .part3, .part4
                width: calc((100% - 400px) / 3)   
        @include xxs
            display: flex
            .part4
                width: 0
            .part2 
                width: calc(100vw - 100px) 
            .part1, .part3
                width: 50px
        
        .wrap__item
            &::before
                opacity: 0.7
    &.active3
        .part1
            left: -2.2%
        .part2
            left: 22.8%
        .part3 
            left: 47.8%
        .part4
            left: 77.2%
        @include sm
            .part3 
                width: 500px 
            .part2, .part1, .part4
                width: calc((100% - 500px) / 3)
        @include xs
            .part3 
                width: 400px 
            .part2, .part1, .part4
                width: calc((100% - 400px) / 3)   
        @include xxs
            display: flex
            .part2 
                &::after
                    display: none
            .part4, .part2 
                width: 50px
            .part3
                width: calc(100vw - 100px) 
            .part1
                width: 0
        .wrap__item
            &::before
                opacity: 0.7
    &.active4
        .part1
            left: -2.2%
        .part2
            left: 22.8%
        .part3 
            left: 47.8%
        .part4
            left: 70.6%
        @include sm
            .part4 
                width: 500px 
            .part2, .part3, .part1
                width: calc((100% - 500px) / 3)
        @include xs
            .part4 
                width: 400px 
            .part2, .part3, .part1
                width: calc((100% - 400px) / 3)   
        @include xxs
            display: flex
            .part2 
                &::after
                    display: none    
            .part4 
                width: calc(100vw - 100px) 
            .part2, .part3
                width: 50px
            .part1
                width: 0
        .wrap__item
            &::before
                opacity: 0.7
    &__inner 
        position: absolute
        top: 50%
        left: 50%
        transform: translate(-50%, -50%)
        z-index: 5
        width: 100%
        display: flex
        flex-direction: column
        align-items: center
        justify-content: center
        pointer-events: none
        display: none
        z-index: 12
        &.hide 
            opacity: 0
            pointer-events: none
            transition: opacity 0.2s ease-out
    &__tap
        width: 70px 
        height: 70px
        border-radius: 100%
        background: #1325F1
        display: flex
        align-items: center
        justify-content: center
        @include xxs 
            width: 50px 
            height: 50px
    &__big-title
        margin: 0
        margin-bottom: 20px
        font-size: 200px
        text-transform: uppercase
        font-family: $teko
        line-height: 140px
        letter-spacing: -0.05em
        text-align: center
        filter: drop-shadow(0px 0px 30px #000000)
        @include xxs 
            font-size: 80px 
            line-height: 56px
    &__item 
        position: relative
        position: absolute
        top: 0
        left: 0
        width: 100%
        height: 100%
        min-width: 0
        display: flex
        justify-content: center
        overflow: hidden
        width: 29.4%
        transition: left 0.2s ease-out
        &:after 
            content: ''
            position: absolute
            top: 0
            left: 0
            width: 100% 
            height: 100%
            border-left: 4px solid #fff 
            background: transparent 
            pointer-events: none
            z-index: 5
        &.part2
            left: 25%
            z-index: 5
        &.part3 
            left: 50%
            z-index: 6
        &.part4 
            left: 75%
            z-index: 7
            &:after 
                border-right: none
        &.part1 
            .wrap__content
                left: 50%
            &:after 
                border-left: none
        &::before
            content: ''
            position: absolute
            top: 0
            left: 0
            width: 100%
            height: 100%
            background: #1325F1
            opacity: 0
            z-index: 5
            pointer-events: none 
        &.part1
            .wrap__content 
                transform: translateY(5%) translateX(-55%)
        &.part2
            .wrap__content 
                transform: translateY(5%) translateX(-52%)
        &.part3
            .wrap__content 
                transform: translateY(5%) translateX(-47%)
        &.part4
            .wrap__content 
                transform: translateY(5%) translateX(-45%)
        @include sm 
            position: relative
            left: 0!important
            transition: width 0.2s ease-out
            width: 25%
        &.active
            &::before
                opacity: 0!important
            .wrap__bg 
                img
                    transform: scale(1)
                    transition: transform 0.2s ease-out
            .wrap__agent 
                width: 100%
                transition: width 0.2s ease-out
                img
                    transform: scale(1.1)
                    transition: transform 0.2s ease-out
                    @include sm 
                        transform: scale(1)
                @include sm 
                    width: 500px
                @media(max-width: 768px)
                    width: 400px
                @include xxs 
                    width: calc(100vw - 100px)
            .wrap__content
                transform: translateX(-50%)
                opacity: 1
                transition: all 0.2s ease-out
    &__same
        position: relative
        @media(min-width: 1921px)
            margin-bottom: -24px
        margin-bottom: -1vw
        img
            width: 100%
        @include md 
            width: 90%
        @include sm 
            margin-bottom: -6%
    &__bg 
        position: absolute
        top: 0
        left: 50%
        transform: translateX(-50%)
        width: 100%
        height: 100%
        overflow: hidden
        @include sm 
            width: 500px
        @include xs 
            width: 400px
        @include xxs 
            width: calc(100vw - 100px)
        img 
            width: 100%
            height: 100%
            object-fit: cover
            transform: scale(1.1)
            transition: transform 0.2s ease-out
            @include sm 
                transform: scale(1)
    &__agent
        position: absolute
        top: 0
        left: 0
        width: 88.5%
        height: 100%
        overflow: hidden
        transform-origin: center
        transition: width 0.2s ease-out
        @include sm 
            width: 500px
            height: 1660px
            min-height: 100%
            left: 50%
            transform: translateX(-50%)
        @media(max-width: 768px)
            width: 400px
            height: 1200px
        @include xxs 
            width: calc(100vw - 100px)
            height: 115%
            transition: width 0.2s ease-out
        img 
            width: 100%
            height: 100%
            object-fit: cover
            object-position: center top
            transition: transform 0.2s ease-out
            transform-origin: top center
            @include sm 
                object-position: 13% top
            @include xxs 
                object-position: 43% top
    &__content
        padding-bottom: 60px
        height: 100%
        width: 80%
        position: absolute
        bottom: 0
        left: calc(50% + 1px)
        display: flex
        flex-direction: column
        justify-content: flex-end
        align-items: center
        transform: translateY(5%) translateX(-55%)
        opacity: 0
        transition: all 0.2s ease-out
        @include lg 
            padding-bottom: 30px
            width: 90% 
        @include sm 
            width: 490px
        @include xs
            width: 390px
        @include xxs 
            width: calc(100vw - 118px)
    &__case
        margin-bottom: -41%
        margin-top: -42%
        position: relative

body.touch 
    .wrap
        &__item
            &::before
                opacity: 0!important
        &__inner
            padding: 0 10px
            display: flex

body.rotate 
    .wrap__agent 
        height: 100%
        width: 300px
    .wrap__content
        width: 200px
        padding-bottom: 20px
    .main-title
        font-size: 36px
        line-height: 25px
    .wrap__same
        margin-bottom: -5%
    .wrap
        &.active1
            .part1 
                width: 300px 
            .part2, .part3, .part4
                width: calc((100% - 300px) / 3)   
        &.active2
            .part2 
                width: 300px 
            .part1, .part3, .part4
                width: calc((100% - 300px) / 3)   
        &.active3
            .part3 
                width: 300px 
            .part2, .part1, .part4
                width: calc((100% - 300px) / 3)   
        &.active4
            .part4 
                width: 300px 
            .part2, .part3, .part1
                width: calc((100% - 300px) / 3)  
        &__item 
            &.active
                .wrap__agent 
                    height: 100%
                    width: 300px
    .btn-main__main
        padding: 11px 42px
        font-size: 16px
        clip-path: polygon(30% 0,calc(100% - 10px) 0,100% 10px,calc(100% - 13px) 100%,70% 100%,10px 100%,0 30px,13px 0)

.btn-main 
    padding: 0
    display: inline-block
    font-size: 24px
    font-family: $bold
    text-align: center
    text-transform: uppercase
    text-decoration: none
    position: relative
    background: none
    white-space: nowrap
    border: none
    flex: none
    line-height: 120%
    cursor: pointer      
    &__main 
        padding: 16px 48px
        display: flex
        align-items: center
        justify-content: center
        transition: background 0.2s ease-out
        background-color: $blue
        clip-path: polygon(30% 0,calc(100% - 13px) 0,100% 10px,calc(100% - 18px) 100%,70% 100%,13px 100%,0 50px,18px 0)
        transition: background-color 0.2s
        position: relative
        z-index: 3
        line-height: 117%
        @include md 
            padding: 11px 31px
            font-size: 16px
            clip-path: polygon(30% 0,calc(100% - 10px) 0,100% 10px,calc(100% - 13px) 100%,70% 100%,10px 100%,0 30px,13px 0)
        @include sm 
            padding: 16px 48px
            font-size: 24px
            clip-path: polygon(30% 0,calc(100% - 12px) 0,100% 10px,calc(100% - 18px) 100%,70% 100%,12px 100%,0 50px,18px 0)
        @include xxs 
            padding: 11px 42px
            font-size: 16px
            clip-path: polygon(30% 0,calc(100% - 10px) 0,100% 10px,calc(100% - 13px) 100%,70% 100%,10px 100%,0 30px,13px 0)
        @media(max-width: 360px)
            padding: 11px 30px
    &__r 
        display: flex
        flex-direction: row-reverse
        height: 100%
        position: absolute
        left: 4%
        top: 0
        width: 95%
        span 
            position: absolute
            flex-shrink: 0
            display: block
            height: 100%
            width: 100%
            clip-path: polygon(30% 0,calc(100% - 12px) 0,100% 10px,calc(100% - 18px) 100%,70% 100%,12px 100%,0 50px,18px 0)
            background-color: $blue
            opacity: 0.1
            transition: transform 0.2s ease-out
            @include md 
                clip-path: polygon(30% 0,calc(100% - 10px) 0,100% 10px,calc(100% - 13px) 100%,70% 100%,10px 100%,0 30px,13px 0)
            @include sm 
                clip-path: polygon(30% 0,calc(100% - 12px) 0,100% 10px,calc(100% - 18px) 100%,70% 100%,12px 100%,0 50px,18px 0)
            @include xxs 
                clip-path: polygon(30% 0,calc(100% - 10px) 0,100% 10px,calc(100% - 13px) 100%,70% 100%,10px 100%,0 30px,13px 0)
    &__l
        display: flex
        flex-direction: row-reverse
        height: 100%
        position: absolute
        left: 1%
        top: 0
        transition: width 0.2s
        width: 95%
        span 
            position: absolute
            flex-shrink: 0
            display: block
            height: 100%
            width: 100%
            clip-path: polygon(30% 0,calc(100% - 12px) 0,100% 10px,calc(100% - 18px) 100%,70% 100%,12px 100%,0 50px,18px 0)
            background-color: $blue
            opacity: 0.1
            transition: transform 0.2s ease-out
            @include md 
                clip-path: polygon(30% 0,calc(100% - 10px) 0,100% 10px,calc(100% - 13px) 100%,70% 100%,10px 100%,0 30px,13px 0)
            @include sm 
                clip-path: polygon(30% 0,calc(100% - 12px) 0,100% 10px,calc(100% - 18px) 100%,70% 100%,12px 100%,0 50px,18px 0)
            @include xxs 
                clip-path: polygon(30% 0,calc(100% - 10px) 0,100% 10px,calc(100% - 13px) 100%,70% 100%,10px 100%,0 30px,13px 0)
    &:hover
        .btn-main__main 
            background: #EB4DC3
            transition: background 0.2s ease-out
        .btn-main__r 
            span 
                background: #EB4DC3
                transition: background 0.2s ease-out,transform 0.2s ease-out
                &:nth-child(1)
                    transform: translateX(22px)
                &:nth-child(2)
                    transform: translateX(40px)
                &:nth-child(3)
                    transform: translateX(57px)
                @include md 
                    &:nth-child(1)
                        transform: translateX(16px)
                    &:nth-child(2)
                        transform: translateX(30px)
                    &:nth-child(3)
                        transform: translateX(44px)
        .btn-main__l
            span 
                background: #EB4DC3
                transition: background 0.2s ease-out,transform 0.2s ease-out
                &:nth-child(1)
                    transform: translateX(-22px)
                &:nth-child(2)
                    transform: translateX(-40px)
                &:nth-child(3)
                    transform: translateX(-57px)
                @include md 
                    &:nth-child(1)
                        transform: translateX(-16px)
                    &:nth-child(2)
                        transform: translateX(-30px)
                    &:nth-child(3)
                        transform: translateX(-44px)